@charset "UTF-8";
a {
  color: #6071d5; }

a:hover, .a:focus {
  color: #6071d5; }

body {
  color: #666;
  max-width: 375px; }

fieldset {
  margin-top: 30px; }

ul {
  margin: 0;
  padding: 0; }
  ul li {
    list-style: none; }

.color-main {
  color: #6071d5; }

.color-gray {
  color: #999; }

.color-orange {
  color: #ff7c08; }

.am-btn-primary, .am-btn-primary:hover, .am-btn-primary:focus, .am-btn-primary[disabled], .am-btn-primary[disabled]:hover {
  background: #6071d5;
  border-color: #6071d5; }

.t-c {
  text-align: center; }

.t-l {
  text-align: left; }

.t-r {
  text-align: right; }

.l {
  float: left; }

.r {
  float: right; }

label.normal {
  font-weight: normal; }

.clearfix:before, .clearfix:after, .cf:before, .cf:after {
  content: "";
  display: table; }

.clearfix:after, .cf:after {
  clear: both; }

p.normal {
  margin: 0;
  padding: 6px 0; }

.twitter-typeahead .typeahead {
  background-color: #fff; }

.twitter-typeahead .tt-query {
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075); }

.twitter-typeahead .tt-hint {
  color: #999; }

.twitter-typeahead .tt-menu {
  width: 422px;
  margin: 12px 0;
  padding: 8px 0;
  background-color: #fff;
  border: 1px solid #ccc;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); }

.twitter-typeahead .tt-value {
  display: block; }

.twitter-typeahead .tt-footer {
  display: block;
  padding: 3px 0;
  font-size: 12px;
  color: #999;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.twitter-typeahead .tt-suggestion {
  padding: 3px 20px;
  font-size: 18px;
  line-height: 24px; }

.twitter-typeahead .tt-suggestion:hover {
  cursor: pointer;
  color: #fff;
  background-color: #0097cf; }
  .twitter-typeahead .tt-suggestion:hover .tt-footer {
    color: #fff; }

.twitter-typeahead .tt-suggestion.tt-cursor {
  color: #fff;
  background-color: #0097cf; }
  .twitter-typeahead .tt-suggestion.tt-cursor .tt-footer {
    color: #fff; }

.twitter-typeahead .tt-suggestion p {
  margin: 0; }

/* breakpoint responsive sets */
@media only screen and (max-width: 640px) {
  .small-screen-hide {
    display: none; } }

.am-form input[type=text]:focus {
  border-color: #6071d5; }

header {
  background: #fff;
  height: 50px;
  position: relative; }
  header .menu {
    position: absolute;
    right: 5px;
    top: 5px; }
    header .menu .am-dropdown-toggle {
      display: block;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 20px; }
    header .menu ul.am-dropdown-content {
      min-width: 100px; }
    header .menu ul.am-dropdown-content > li > a {
      padding: 0 0 0 5px;
      font-size: 14px;
      height: 30px;
      line-height: 30px;
      color: #666; }
  header.main .logo {
    position: absolute;
    left: 10px;
    top: 10px; }
    header.main .logo a {
      display: block;
      width: 80px;
      height: 30px;
      background: url("../images/default/logo.png") no-repeat;
      background-size: 80px 30px; }
  header.main .search-go {
    margin: 0 50px 0 100px;
    padding-top: 10px; }
    header.main .search-go .search-go-panel {
      display: block;
      background: #f0f0f0;
      height: 30px;
      width: 100%;
      border-radius: 4px;
      position: relative; }
      header.main .search-go .search-go-panel span {
        position: absolute;
        height: 20px;
        display: block;
        line-height: 20px;
        left: 5px;
        top: 5px;
        font-size: 12px;
        color: #666; }
      header.main .search-go .search-go-panel i {
        position: absolute;
        height: 20px;
        display: block;
        line-height: 20px;
        right: 5px;
        top: 5px;
        font-size: 14px;
        color: #ddd; }
  header.sub .back {
    position: absolute;
    left: 5px;
    top: 5px; }
    header.sub .back a {
      display: block;
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px; }
    header.sub .back i {
      font-size: 24px;
      font-weight: normal;
      color: #6071d5; }
  header.sub .title {
    font-size: 18px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    margin: 0 50px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: #666;
    font-weight: normal; }

footer {
  padding: 15px 0;
  text-align: center;
  color: #bababa; }
  footer p {
    margin: 0;
    font-size: 11px; }
    footer p strong {
      font-size: 12px;
      color: #999; }

.am-form .am-form-icon i {
  color: #6071d5; }

#search-box .am-offcanvas-bar {
  width: auto;
  right: 0;
  background: #fff; }
  #search-box .am-offcanvas-bar:after {
    background: none; }

#search-box .search-title {
  position: relative;
  height: 40px;
  background: #6071d5; }

#search-box .search-select {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0; }
  #search-box .search-select .am-btn {
    font-size: 14px;
    display: block;
    height: 40px;
    width: 80px;
    margin: 0;
    padding: 0;
    background: none;
    border: none;
    color: #fff;
    outline: none; }
    #search-box .search-select .am-btn:active {
      box-shadow: none; }
    #search-box .search-select .am-btn:hover {
      border: none; }
    #search-box .search-select .am-btn em {
      font-style: normal; }
  #search-box .search-select ul.am-dropdown-content {
    min-width: 120px;
    font-size: 12px; }
    #search-box .search-select ul.am-dropdown-content .am-active a {
      background: #6071d5 !important; }

#search-box .search-close {
  z-index: 1;
  position: absolute;
  right: 0;
  top: 0; }
  #search-box .search-close a {
    font-size: 14px;
    color: #fff;
    display: block;
    line-height: 40px;
    text-align: center;
    height: 40px;
    width: 80px; }

#search-box .search {
  margin: 0 80px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  padding-top: 7px;
  position: relative; }
  #search-box .search .submit {
    position: absolute;
    margin: 0;
    display: block;
    padding: 0;
    right: 0;
    top: 7px;
    width: 26px;
    height: 26px;
    border: none;
    outline: 0;
    background: none; }

#search-box .search-input {
  outline: 0;
  display: block;
  width: 100%;
  height: 26px;
  font-size: 12px;
  border: none;
  border-radius: 3px;
  color: #666;
  padding: 0 30px 0 5px; }

#search-box .search-history h3, #search-box .search-hot h3 {
  background: #f5f5f5;
  margin: 0;
  height: 40px;
  line-height: 40px;
  padding-left: 10px;
  font-size: 14px;
  font-weight: normal; }

#search-box .search-history li {
  border-bottom: 1px solid #f5f5f5; }
  #search-box .search-history li a {
    display: block;
    height: 36px;
    line-height: 36px;
    padding-left: 10px;
    font-size: 14px; }

#search-box .search-history .clear-history {
  height: 40px;
  display: block;
  text-align: center;
  line-height: 40px;
  color: #999; }

#search-box .search-hot h3 {
  margin-bottom: 10px; }

#search-box .search-hot div {
  padding-left: 10px; }
  #search-box .search-hot div a {
    margin: 0 10px 10px 0;
    display: inline-block;
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
    border: 1px solid #ddd;
    border-radius: 3px;
    font-size: 14px; }

/* login page */
.login-page .am-form {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 90%;
  margin: auto; }

.login-page .am-form-group {
  margin-bottom: 5px; }

.login-page .get-smscode, .login-page .infos {
  position: absolute;
  right: 0.5em;
  top: 50%;
  display: block;
  margin-top: -.5em;
  line-height: 1;
  z-index: 2; }
  .login-page .get-smscode em, .login-page .infos em {
    display: none;
    font-style: normal; }
    .login-page .get-smscode em b, .login-page .infos em b {
      font-weight: normal; }
  .login-page .get-smscode.on a, .login-page .infos.on a {
    display: none; }
  .login-page .get-smscode.on em, .login-page .infos.on em {
    display: inline; }

.login-page .am-btn {
  display: block;
  width: 100%;
  color: #fff; }

.login-page .message {
  color: #f40;
  height: 20px;
  line-height: 20px;
  margin-bottom: 8px; }
  .login-page .message .error {
    color: #f40;
    font-weight: normal; }
  .login-page .message.t-r {
    margin-bottom: 15px; }

.login-page .wechat {
  display: inline-block;
  width: 36px;
  height: 36px;
  border-radius: 36px;
  background: #2ed441;
  text-align: center;
  line-height: 36px; }
  .login-page .wechat i {
    font-size: 20px;
    color: #fff; }

.login-page .line {
  border-top: 1px solid #ccc;
  margin: 40px auto 0 auto;
  width: 90%;
  height: 30px;
  position: relative; }
  .login-page .line span {
    display: block;
    width: 240px;
    height: 30px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    background: #fff;
    position: absolute;
    top: -15px;
    left: 50%;
    line-height: 30px;
    margin-left: -120px;
    font-size: 15px; }

.login-page .form-userinfo .am-form-group {
  margin-bottom: 15px; }

/* index page */
.index-page {
  /* 图片轮播css已完成 amazeui */ }
  .index-page .am-slider {
    box-shadow: none;
    overflow: hidden; }
    .index-page .am-slider .am-control-nav {
      bottom: 0;
      vertical-align: middle;
      height: 30px;
      line-height: 30px; }
      .index-page .am-slider .am-control-nav li {
        vertical-align: middle; }
        .index-page .am-slider .am-control-nav li a {
          box-shadow: none;
          background: #fff; }
          .index-page .am-slider .am-control-nav li a.am-active {
            background: #6071d5; }
  .index-page .nav li {
    width: 25%;
    float: left;
    margin-bottom: 10px; }
    .index-page .nav li a {
      font-size: 13px;
      display: block;
      text-align: center; }
      .index-page .nav li a img {
        display: block;
        margin: 0 auto 5px auto;
        width: 50%; }
